<template>
    <div>
        <div class="container mt-6 mb-6">
            <p class="p-4 text-2xl font-semibold">Fasilitas</p>
            <div class="row justify-content-center">
                <div class="col-4" v-for="(item, index) in datanya" :key="index+'kategori'">
                    <div class="bg-white shadow rounded-lg p-2 cursor-pointer" @click="$router.push(`/fasilitas/detail?idkategori='${item.id}'`)"> 
                        <div class="bg-white shadow rounded-lg p-2">
                            <img :src="item.gambar" style="width:100%;height:250px;" alt="">
                            <div style="position:absolute;bottom:30px;left:40px;">
                                <p class="font-semibold text-red-500 text-lg">{{item.kategori}}</p>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>
<script>
import autophp from '@/plugins/autophp';
let sdb = new autophp();
export default{
    layout:'app_landing_estate2',
    data(){
        return{
            datanya:[]
        }
    },
    methods: {
        getData(){
            sdb.collection("app_gis_fasilitas_kategori",false).doc().select(`select * from app_gis_fasilitas_kategori`).then(res => {
                console.log(res);
                this.datanya=res;
                this.$forceUpdate();
            });
            
        }
    },
    mounted() {
        this.getData();
    },
}
</script>